import axios from './axios'

/*
 * 
 *  url 
 */

// 
export const login = data => {
  return axios({
    url: 'sys/login',
    method: 'post',
    data
  })
}

export const getUser = () => {
  return axios({
    url: '/user',
    method: 'get'
  })
}

export const getMenu = data => {
  return axios({
    url: '/menu',
    method: 'post',
    data
  })
}

// 
export default {
  login,
  getUser,
  getMenu
}



