exports.formatMoney = (value) => {
  if (value) {
    value = parseFloat(value);
    return ' ' + value.toFixed(2);
  }
};

exports.getOrderStatusName = (val) => {
  let statusName = '';
  switch (val) {
    case 0: {
      statusName = '';
      break;
    }
    case 10: {
      statusName = '';
      break;
    }
    case 20: {
      statusName = '';
      break;
    }
    case 40: {
      statusName = '';
      break;
    }
    case '50': {
      statusName = '';
      break;
    }
    case '60': {
      statusName = '';
      break;
    }
  }
  return statusName;
};



